.section-blockinfo {
	margin-top: rem(124);
	margin-bottom: rem(60);

	border-top: rem(0.5) solid var(--c-black-20);
	border-bottom: rem(0.5) solid var(--c-black-20);

	padding: rem(34) 0;

	@include min768 {
		margin-top: rem(62);
		margin-bottom: rem(56);
	}

	.title-18 {
		margin-bottom: rem(20);

		@include min540 {
			max-width: rem(225);
		}
	}

	p {
		color: var(--c-black-70);
		line-height: 160%;

		@include min768 {
			font-size: rem(14);
		}
	}
}