.page-product {
	.advantages {
		@include min540 {
			display: none;
		}
	}

	.section-watch {
		@include min {
			display: none;
		}
	}
}

.product {
	&__slider {
		flex: 0 0 rem(588);
		max-width: rem(588);

		@include min1280 {
			flex: 0 0 55%;
			max-width: 55%;
		}

		@include min1050 {
			flex: 0 0 60%;
			max-width: 60%;
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: rem(24);
		}

		.not-image {
			height: rem(65);

			@include min768 {
				height: rem(55);
			}
		}

		&-main {
			height: rem(366);
			margin-bottom: rem(20);

			@include min540 {
				height: rem(183);
				margin-bottom: rem(12);
			}

			.swiper-nav-wrap {
				display: none;

				@include min540 {
					position: absolute;
					top: 50%;
					margin-top: rem(-6);
					width: 100%;
					z-index: 5;

					display: flex;
					justify-content: space-between;

					.swiper-nav-prev {
						margin-left: rem(7);
					}

					.swiper-nav-next {
						margin-right: rem(7);
					}
				}
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.not-image {
				height: rem(366);

				@include min540 {
					height: rem(183);
				}

				span {
					font-size: rem(16);
				}
			}
		}

		&-thumbs {
			height: rem(65);

			.swiper-slide {
				cursor: pointer;
			}

			img {
				width: rem(100);
				height: rem(65);
				object-fit: cover;
				border-radius: rem(2);

				@include min768 {
					width: 100%;
					height: rem(55);
				}
			}
		}
	}

	&__list {
		padding-bottom: rem(26);
		margin-bottom: rem(22);
		border-bottom: rem(0.5) solid var(--c-black-20);

		ul {
			display: flex;
			flex-direction: column;

			li {
				display: flex;
				flex-wrap: wrap;
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}

				b,
				span {
					font-size: rem(10);
					line-height: 120%;
				}

				b {
					flex: 0 0 rem(140);
					max-width: rem(140);
					font-weight: 600;

					@include min540 {
						flex: 0 0 rem(200);
						max-width: rem(200);
					}
				}
			}
		}
	}

	&__info {
		margin-left: rem(24);
		flex: 0 0 rem(280);
		max-width: rem(280);

		display: flex;
		flex-direction: column;
		
		@include min1280 {
			flex: 0 0 40%;
			max-width: 40%;
		}

		@include min1050 {
			flex: 0 0 37%;
			max-width: 37%;
			margin-left: 0;
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-left: 0;
		}

		.title-24 {
			padding-bottom: rem(20);
			margin-bottom: rem(26);
			border-bottom: rem(0.5) solid var(--c-black-20);
		}

		&-price {
			padding-bottom: rem(30);
			margin-bottom: rem(26);
			border-bottom: rem(0.5) solid var(--c-black-20);

			display: flex;
			flex-direction: column;

			.title-18 {
				margin-bottom: rem(6);
			}

			.subtitle {
				font-size: rem(12);
				color: var(--c-black-20);
				margin-bottom: rem(12);
			}

			.button-icon {
				align-self: baseline;
			}

		}

		

		&-buttons {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.button-red {
				margin-right: rem(18);

				@include min {
					margin-right: rem(4);
				}
			}
		}
	}


	.tab_wrap {
		margin-top: rem(77);
		margin-bottom: rem(34);

		&__nav {
			margin-bottom: rem(32);
		}
	}

	.section-related {

		// .section-header {
		// 	margin-bottom: rem(26);
		// }
	}
}



.not-image {
	border-radius: rem(2);
	background-color: white;
	border: rem(0.5) solid var(--c-black-20);
	
	display: flex;
	align-items: center;
	justify-content: center;

	span {
		font-size: rem(10);
		color: var(--c-black-20);
		text-transform: lowercase;
	}
}