.section-reviews {
	margin-bottom: rem(64);

	@include min540 {
		margin-bottom: 0;
	}

	.control-mt {
		.section-reviews__control {
			margin-top: rem(80);

			@include min540 {
				margin-top: rem(40);
			}
		}
	}
	
	.swiper-container {
		margin: rem(-20);
		padding: rem(20);

		@include min540 {
			margin: 0;
			padding: 0;
		}
	}

	.group-items-3 {
		margin-bottom: 0;
		.item_product {
			width: 100%;

			margin: 0 !important;

			@include min540 {
				// padding-right: rem(10) !important;
				padding-left: rem(18) !important;
			}
		}
	}

	.section-header {
		.flex {
			margin-bottom: 0;
		}
	}

	@include min768 {
		.swiper-container {
			.swiper-nav-prev,
			.swiper-nav-next {
				position: absolute;
				top: rem(-36);
				margin-right: rem(-20);

				width: rem(44);
				height: rem(44);

				i {
					font-size: rem(12);
				}
			}

			.swiper-nav-prev {
				left: 0;
			}

			.swiper-nav-next {
				right: rem(20);
			}
		}
	}

	&__wrap {
		@include min540 {
			margin-right: rem(-14) !important;
			padding-right: rem(20) !important;

			margin-left: rem(-14) !important;
			padding-left: rem(40) !important;

			margin-top: rem(-14);
			padding-top: rem(14) !important;

			.swiper-slide-active {
				.review_item {
					border: 0;
					box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.07),
						0px 4px 25px rgba(0, 0, 0, 0.1);

					.review_item__image {
						i {
							&::before {
								color: #FF7B7B;
							}
						}
					}

					.review_item__text {
						color: rgb(33, 33, 33);
					}

					.review_item__author {
						color: black;
					}
				}
			}

			.swiper-nav-prev {
				left: rem(-24) !important;
			}

			.swiper-nav-next {
				right: rem(14) !important;
			}
		}
	}

	// @include min540 {
	// 	.swiper-container {
			

	// 		.swiper-wrapper {
	// 			// margin-right: rem(40);
	// 			// margin-left: rem(40);
	// 		}
			
			
	// 	}

		
	// }

	&__control {
		position: relative;
		margin-top: rem(18);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		
		height: rem(44);

		@include min540 {
			margin-top: rem(34);
		}

		.swiper-nav-wrap {
			position: absolute;
			bottom: 0;
			left: 0;

			@include min768 {
				width: 100%;
				bottom: rem(9);
				justify-content: space-between;
			}
		}

		.swiper-nav-prev,
		.swiper-nav-next {
			box-shadow: none;
		}

		.swiper-pagination {
			position: relative;

			@include min768 {
				max-width: rem(135);
				flex-wrap: nowrap;

				&-bullet {
					margin-right: rem(4);
				}
			}

			
		}
	}
}

.review_item {
	border-radius: rem(5);
	border: rem(0.5) solid rgba(0, 0, 0, 0.2);
	background-color: white;
	display: flex;
	flex-direction: column;

	height: rem(282);

	padding: rem(14) rem(20) rem(20) rem(23);

	transition: all 250ms ease;

	@include min540 {
		padding: rem(10) rem(27);
	}

	&:hover {
		box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.07),
		0px 4px 25px rgba(0, 0, 0, 0.1);
		
		.review_item__image {
			i {
				&::before {
					color: #FF7B7B;
				}
			}
		}

		.review_item__text {
			color: rgb(33, 33, 33);
		}

		.review_item__author {
			color: black;
		}


	}

	&__image {
		position: relative;
		margin-bottom: rem(16);

		img {
			max-width: rem(100);
			border-radius: rem(5);
			object-fit: cover;
		}

		i {
			font-size: rem(30);

			position: absolute;
			bottom: rem(-8);
			left: 0;

			&::before {
				color: #ECECEC;
			}
		}
	}

	&__text, &__author {
		transition: all 250ms ease;
	}

	&__text {
		font-size: rem(13);
		line-height: 162%;
		color: rgba(33,33,33,.5);
		max-width: rem(233);
		
	}

	&__author {
		margin-top: auto;
		font-size: rem(12);
		font-weight: 500;
		line-height: 130%;
		color: var(--c-black-60);
	}
}