.footer {
	position: relative;
	margin-top: rem(75);

	&__arrow-top {
		cursor: pointer;
		position: absolute;
		top: 50%;
		margin-top: rem(-25);
		left: rem(10);

		width: rem(50);
		height: rem(50);
		border-radius: 50%;
		box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.1),
		0px 0px 10px 0px rgba(174, 174, 174, 0.07);

		display: flex;
		align-items: center;
		justify-content: center;

		transition: all 250ms ease;

		@include min768 {
			display: none;
		}

		&:hover {
			box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.4),
			0px 0px 10px 0px rgba(174, 174, 174, 0.25);

		}

		i {
			font-size: rem(23);
		}
	}

	&__top {
		border-top: rem(0.5) solid #D2D2D2;
		

		.container {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		&-left {
			display: flex;
			flex-direction: column;
			padding-top: rem(26);
			padding-bottom: rem(52);
			padding-right: rem(35);
			border-right: rem(0.5) solid #D2D2D2;

			flex: 0 0 rem(466);
			max-width: rem(466);

			@include min1050 {
				flex: 0 0 50%;
				max-width: 50%;
			}

			@include min540 {
				flex: 0 0 100%;
				max-width: 100%;
				order: 2;
				border-right: 0;
			}

			.logo {
				margin-bottom: rem(25);

				@include min540 {
					display: none;
				}
			}

			.time {
				margin-bottom: rem(30);
				font-weight: 600;
			}

			.mail {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(34);

				&:hover {
					color: var(--c-red);
				}

				i {
					margin-right: rem(7);
				}
			}

			.links {
				display: flex;
				// flex-wrap: wrap;
				margin-bottom: rem(50);

				@include min1050 {
					flex-wrap: wrap;
					// flex-direction: column;
					// align-self: baseline;
					margin-bottom: rem(38);
				}

				&:last-of-type {
					margin-bottom: 0;
				}

				ul {
					display: flex;
					flex-direction: column;

					li {

						margin-bottom: rem(9);

						&:last-child {
							margin-bottom: 0;
							align-self: baseline;

							a {
								font-size: rem(10);
								font-weight: 500;
								color: var(--c-black-50);
								border-bottom: rem(1) solid var(--c-black-50);

								&:hover {
									color: black;
									border-color: black;
								}
							}
						}

						a {
							&:hover {
								color: var(--c-red);
							}
						}
					}
				}

				.social {
					a {
						margin-right: rem(5);

						&:hover {
							opacity: .8;
						}

						&:last-child {
							margin-right: 0;
						}
					}
				}

				.pay {
					span {
						margin-bottom: rem(1);
						font-size: rem(12);
						font-weight: 500;
						color: var(--c-black-50);
					}

					&__images {

						display: flex;
						flex-wrap: wrap;
						align-items: center;

						img {
							margin-right: rem(14);

							&:last-child {
								margin-right: 0;
							}
						}
					}
				}


				&__left {
					flex: 0 0 rem(200);
					max-width: rem(200);

					@include min1050 {
						margin-bottom: rem(38);
					}
				}

				&__right {
					flex: 0 0 rem(240);
					max-width: rem(240);
				}
			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			padding-top: rem(64);
			// padding-left: rem(146);

			@include min1050 {
				flex: 0 0 50%;
				max-width: 50%;

				flex-direction: column;

				padding-top: rem(44);
				padding-left: rem(86);
			}

			@include min768 {
				padding-left: rem(42);
			}

			@include min540 {
				flex: 0 0 calc(100% + 28px);
				max-width: calc(100% + 28px);

				margin-left: rem(-14);
				margin-right: rem(-14);

				padding-left: rem(14);
				padding-right: rem(28);

				order: 1;
				// padding-left: 0;
				padding-top: rem(30);

				border-bottom: rem(0.5) solid #D2D2D2;
			}

			ul {
				margin-right: rem(90);

				@include min1050 {
					margin-bottom: rem(50);
				}

				@include min540 {
					margin-bottom: rem(27);
				}

				&:last-child {
					margin-right: 0;
				}

				li {
					font-size: rem(16);
					line-height: 160%;

					color: var(--c-black-60);

					@include min540 {
						font-size: rem(12);
					}

					&:first-child {
						font-weight: bold;
						color: black;
					}

					a {
						&:hover {
							color: var(--c-red);
						}
					}
				}
			}
		}
	}

	&__bottom {
		border-top: rem(0.5) solid #D2D2D2;

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			padding-top: rem(19);
			padding-bottom: rem(24);

		}

		&_left, &_center, &_right {

			@include min1050 {
				flex: 0 0 33.333%;
				max-width: 33.333%;
			}

			@include min540 {
				flex: 0 0 100%;
				max-width: 100%;
			}
			
		}

		&_left {
			@include min1050 {
				p {
					max-width: rem(185);
				}
			}

			@include min540 {
				margin-bottom: rem(34);
			}
		}

		&_center {
			@include min1050 {
				text-align: center;
			}

			@include min540 {
				margin-bottom: rem(34);
				text-align: left;
			}
		}

		p, a, span  {
			font-size: rem(12);
			color: var(--c-black-40);

			transition: all 250ms ease;
		}

		

		a {

			@include min1050 {
				text-align: center;
			}

			&:hover {
				color: black;
			}
		}

		.logo {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min1050 {
				justify-content: flex-end;
			}

			@include min540 {
				justify-content: flex-start;
			}

			&:hover {
				span {
					color: black;
				}
			}

			span {
				margin-right: rem(7);
				white-space: nowrap;

				@include min1050 {
					display: block;
				}
			}
		}
	}
}