.sect_slider {
	
	margin-bottom: rem(14);

	.container {
		position: relative;
	}
	
	.swiper-container {
		border-radius: rem(10);

		.swiper-pagination {
			position: absolute;
			width: auto;
			bottom: rem(40);
			left: rem(125);

			@include min1050 {
				bottom: rem(15);
				left: rem(86);
			}

			@include min540 {
				width: 100%;
				bottom: rem(20);
				left: auto;

				display: flex;
				justify-content: center;
			}

			&-bullet {
				opacity: 1;
				width: rem(68) !important;
				border-radius: rem(2) !important;
				height: rem(2) !important;
				background-color: rgba(255,255,255,.5) !important;

				margin: 0 rem(10) 0 0 !important;

				@include min540 {
					width: rem(33) !important;
					margin: 0 rem(4) 0 0 !important;
				}

				&:last-child {
					margin: 0;
				}
			}

			&-bullet-active {
				background-color: white !important;
			}
		}
		
		
	}

	.swiper-nav-prev,
	.swiper-nav-next {
		position: absolute;
		top: 50%;
		z-index: 10;
		margin-top: rem(-9);

		@include min1050 {
			box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25),
				0 4px 25px 0 rgba(0, 0, 0, 0.25);
		}

		@include min540 {
			top: auto;
			bottom: rem(9);
		}
	}

	.swiper-nav-prev {
		left: rem(18);

		@include min1050 {
			left: rem(-22);
		}

		@include min {
			left: rem(18);
		}

		@include min540 {
			left: rem(7);
		}
	}

	.swiper-nav-next {
		right: rem(18);

		@include min1050 {
			right: rem(-22);
		}

		@include min {
			right: rem(18);
		}

		@include min540 {
			right: rem(7);
		}
	}

	&__item {
		position: relative;
		height: rem(396);
		padding: rem(82) rem(125);
		border-radius: rem(10);
		background: linear-gradient(89.57deg, rgba(0, 0, 0, 0.63) 11.27%, rgba(125, 125, 125, 0.24) 99.68%);

		@include min1050 {
			height: rem(276);

			padding: rem(40) rem(88);
		}

		@include min540 {
			height: rem(425);
			padding: rem(40) rem(30);
		}

		&-image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			z-index: -1;
			height: 100%;
			object-fit: cover;
		}

		.title-32 {
			color: white;
		}

		.text,
		.button-gray-tr {
			margin-top: rem(14);

			@include min1050 {
				margin-top: rem(5);
			}

			@include min540 {
				margin-top: rem(14);
			}
		}

		.text {
			max-width: rem(380);

			font-weight: 500;
			line-height: 140%;
			color: white;

			@include min1050 {
				margin-bottom: rem(14);
				font-size: rem(14);
			}

			@include min540 {
				max-width: rem(220);
			}
		}

		// .button-gray-tr {
		// 	display: inline-block;
		// }
	}

}

.swiper-nav-wrap {
	display: flex;
	flex-wrap: wrap;

	.swiper-nav-prev {
		margin-right: rem(11);
	}
}

.swiper-nav-prev,
.swiper-nav-next {
	width: rem(44);
	height: rem(44);
	border-radius: 50%;

	border: solid rem(0.5) rgba(0, 0, 0, 0.1);
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25),
	0 4px 25px 0 rgba(0, 0, 0, 0.25);
	background-color: #ffffff;

	display: flex;
	align-items: center;
	justify-content: center;

	@include min768 {
		width: rem(26);
		height: rem(26);
	}

	i {
		font-size: rem(11);

		@include min768 {
			font-size: rem(6);
			
			&::before {
				color: black;
			}
		}
	}
}
.swiper-nav-next {
	i {
		margin-left: rem(3);
	}
}
.swiper-button-disabled {
	i {
		&::before {
			color: #595959;
			opacity: .3;
		}
	}
}

.swiper-pagination {
	display: flex;
	flex-wrap: wrap;

	&-bullet {
		opacity: 1 !important;
		width: rem(37) !important;
		height: rem(2) !important;
		border-radius: rem(2) !important;
		background-color: var(--c-black-50) !important;

		margin-right: rem(12);

		&:last-child {
			margin-right: 0;
		}
	}

	&-bullet-active {
		background-color: black !important;
	}
}