.section-about {
	// height: rem(508);
	margin-bottom: rem(104);
	position: relative;
	background: linear-gradient(89.57deg, rgba(0, 0, 0, 0.63) 0.41%, rgba(46, 40, 40, 0.74) 99.68%);
	padding: rem(35) rem(86) rem(44) rem(96);

	@include min1050 {
		margin-bottom: rem(90);
	}

	@include min768 {
		margin-bottom: rem(67);
		
		padding: rem(46) rem(14);

		margin-left: rem(-14);
		margin-right: rem(-14);
	}

	&__image {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__content {
		position: relative;
		border-radius: rem(10);
		background: rgba(255, 255, 255, 0.2);
		

		padding: rem(36) rem(48);

		@include min768 {
			padding: 0;
			background-color: transparent;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			backdrop-filter: blur(10px);
			z-index: -1;

			@include min768 {
				display: none;
			}
		}

		img {
			margin-bottom: rem(73);

			@include min768 {
				margin-bottom: rem(67);
			}
		}

		.title-24 {
			margin-bottom: rem(10);
			color: white;

			@include min1050 {
				font-size: rem(24);
			}

			@include min768 {
				font-size: rem(18);
				margin-bottom: rem(18);
			}
		}

		p {
			line-height: 140%;
			color: white;

			margin-bottom: rem(28);

			@include min768 {
				font-size: rem(12);
				margin-bottom: rem(22);
			}

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}