.header {
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25),
	0 4px 25px 0 rgba(0, 0, 0, 0.25);
	margin-bottom: rem(32);

	&__top {
		background-color: #f3f3f3;
		height: rem(40);
		padding: rem(10) 0 rem(13) 0;

		@include min1050 {
			height: auto;
		}

		@include min768 {
			display: none;
		}

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
		}

		&-change {
			display: flex;
			align-items: center;
			i {
				font-size: rem(16);
				margin-right: rem(2.5);
			}

			.text {
				font-weight: 500;
			}
		}

		&-text {
			margin-right: auto;
			margin-left: rem(59);

			@include min1050 {
				max-width: rem(165);
			}

			p {
				font-weight: 500;
				font-size: rem(12);

				@include min1050 {
					font-size: rem(10);
				}
			}
		}

		&-nav {
			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(38);

					@include min1050 {
						margin-right: rem(28);
					}

					&:last-child {
						margin-right: 0;
					}

					a {
						color: var(--c-black-50);

						@include min1050 {
							font-size: rem(12);
						}

						&:hover {
							color: black;
						}
					}
				}
			}
		}
	}

	&__center {
		padding-top: rem(9);
		padding-bottom: rem(28.5);

		@include min1280 {
			padding-top: rem(22);
		}

		@include min768 {
			padding-top: rem(9);
			padding-bottom: rem(9);
		}

		@include min540 {
			padding-top: 0;
			padding-bottom: 0;
		}

		.container {
			display: flex;
			// flex-wrap: wrap;
			justify-content: space-between;
			align-items: flex-end;

			@include min768 {
				position: relative;
				align-items: center;
			}
		}

		&-burger {
			display: none;
			flex-direction: column;
			justify-content: space-between;

			width: rem(24);
			height: rem(20);
			order: 0;

			margin-right: rem(22);

			@include min768 {
				display: flex;
			}

			@include min540 {
				width: rem(16);
				height: rem(14);
				margin-right: rem(17);
			}

			.line {
				width: rem(24);
				height: rem(2);
				border-radius: rem(2);
				background-color: var(--c-red);
				display: block;

				@include min540 {
					width: rem(16);
					height: rem(1);
				}
			}
		}

		&-logo {
			margin-right: rem(43);
			height: rem(59.5);

			@include min1280 {
				max-width: rem(111);
				height: auto;
			}

			@include min768 {
				order: 2;
				position: absolute;
				left: 50%;
				margin-left: rem(-56);
			}

			@include min540 {
				max-width: rem(82);
				margin-left: rem(-41);
			}

			a {
				display: block;
			}
		}

		&-search {
			width: 100%;
			max-width: rem(373);
			margin-right: rem(67);

			@include min1280 {
				max-width: rem(265);
				margin-right: rem(52);
			}

			@include min1120 {
				max-width: rem(43);
			}

			@include min {
				margin-right: rem(42);
			}

			@include min768 {
				order: 1;
				margin-right: auto;
			}

			.form_search {
				position: relative;

				@include min1120 {
					display: none;
				}

				&-field {
					width: 100%;
					height: rem(43);
					border-radius: rem(2);
					border: solid rem(0.5) var(--c-black-10);
					padding: rem(13) rem(38) rem(13) rem(23);
					background-color: white;
					color: var(--c-gray-field);

					@include min1120 {
						display: none;
					}

					@include placeholder {
						color: var(--c-gray-field);
					}

					&:focus {
						border-color: black;
					}
				}

				&-button {
					position: absolute;
					top: 50%;
					right: rem(11);

					margin-top: rem(-11);

					@include min1120 {
						position: relative;
						top: auto;
						right: auto;

						width: rem(43);
						height: rem(43);

						border-radius: rem(2);
						border: solid rem(0.5) var(--c-black-10);

						display: flex;
						align-items: center;
						justify-content: center;
					}

					

					&:hover {
						opacity: .8;
					}

					&:active {
						opacity: 1;
					}

					i {
						font-size: rem(15);
					}
				}
			}

			.form_search_smart_target {
				display: none;
				@include min1120 {
					width: rem(43);
					height: rem(43);

					border-radius: rem(2);
					border: solid rem(0.5) var(--c-black-10);

					display: flex;
					align-items: center;
					justify-content: center;
				}

				@include min768 {
					border: 0;

					i {
						font-size: rem(24);
					}
				}

				@include min540 {
					i {
						font-size: rem(16);
					}
				}
			}
		}

		&-contact {
			margin-right: rem(65);
			padding-bottom: rem(5);

			@include min1280 {
				margin-right: rem(42);
			}

			@include min1050 {
				margin-right: rem(32);
			}

			@include min768 {
				display: none;
			}

			ul {
				display: flex;
				flex-direction: column;

				li {
					margin-bottom: rem(7);
					color: var(--c-black-60);
					font-size: rem(12);

					white-space: nowrap;

					&:last-child {
						margin-bottom: 0;
					}

					a {
						display: flex;
						align-items: center;
						white-space: nowrap;

						&:hover {
							color: black;

							i::before {
								color: black;
							}
						}

						i {
							margin-right: rem(7);
							font-size: rem(10);
							margin-top: rem(2);
							
							&::before {
								color: var(--c-black-60);
							}
						}
					}
				}
			}
		}

		&-phone {
			margin-right: rem(20);
			
			@include min768 {
				display: none;
			}

			ul {
				display: flex;
				flex-direction: column;

				li {
					margin-bottom: rem(8);

					white-space: nowrap;

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-size: rem(16);
						font-weight: 600;
						

						@include min1050 {
							font-size: rem(14);
						}

						&:hover {
							color: var(--c-red);
						}
					}
				}
			}
		}

		&-button {

			@include min768 {
				order: 3;
			}

			.button-red-border {
				padding: rem(14.5) rem(19);
				white-space: nowrap;

				@include min540 {
					padding: rem(6) rem(9);
					display: flex;
					align-items: center;

					span {
						font-size: rem(8);
					}
				}
			}
		}
	}

	&__nav {

		@include min1050 {
			position: relative;
		}
		

		&-button {
			display: none;

			@include min1050 {
				position: absolute;
				top: rem(7);
				right: rem(53);
				z-index: 2;
				width: rem(34);
				height: rem(34);

				display: flex;
				align-items: center;
				justify-content: center;

				i {
					font-size: rem(18);
				}
			}
		}
		

		border-top: rem(0.5) solid #D2D2D2;

		ul {
			display: flex;
			// flex-wrap: wrap;
			align-items: center;

			padding-top: rem(5);
			padding-bottom: rem(5);

			@include min768 {
				justify-content: center;
				padding: 0;
			}

			> li {
				margin-right: rem(32);

				@include min768 {
					display: none;
				}

				@include min1050 {
					margin-right: 0;
				}

				&:last-child {
					margin-right: 0;
					a {
						padding-right: 0;
					}
				}

				&:first-child {
					margin-right: auto;

					@include min1050 {
						margin-right: rem(160);
					}

					@include min {
						margin-right: rem(82);
					}

					@include min768 {
						display: block;
						margin-right: 0;
					}

					a {
						padding-left: 0;

						@include min768 {
							padding-left: rem(15);
							font-weight: 600;
						}

						i {
							display: none;
							@include min768 {
								display: block;
							}
						}
					}
				}

				> a {
					font-size: rem(16);
					font-weight: 500;

					padding: rem(15) rem(16);
					border-radius: rem(5);
					
					display: flex;
					align-items: center;

					white-space: nowrap;

					@include min1050 {
						font-size: rem(14);
					}

					@include min540 {
						font-size: rem(10);
						padding: rem(10) rem(15);
					}

					&:hover {
						color: var(--c-red);
					}

					.burger {
						margin-right: rem(8);
					}
					
					i {
						font-size: rem(6);
						margin-left: rem(4);

						@include min540 {
							font-size: rem(4);
						}
					}
				}

				
			}

			.smart-last, .smart-first {

				@include min1050 {
					position: relative;
					opacity: 0;
				}
			}

			.smart-last {
				@include min1050 {
					display: none;
				}
			}

			.smart-first {

				.drop-menu-content {
					display: none;
				}

				@include min1050 {

					.drop-menu-content {
						display: none;
						right: rem(20);
						left: auto;
						width: rem(153);
					}

					.drop-menu-content.active {
						display: block;
					}
					
					opacity: 1;

					&::before {
						content: "";
						width: rem(34);
						height: rem(140);

						display: block;
						position: absolute;
						top: rem(-48);
						left: rem(8);
						transform: rotate(90deg);
						background: linear-gradient(180deg, #FFFFFF 35.94%, rgba(255, 255, 255, 0) 100%);
					}
				}
			}

			.drop-menu {
				position: relative;

				

				&-content {
					position: absolute;
					left: 0;
					top: 100%;
					z-index: 10;

					margin-top: rem(9);

					width: 100%;
					background-color: white;
					box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
					0px 4px 4px rgba(0, 0, 0, 0.25);
					border-radius: rem(5);

					// display: flex;
					display: none;
					flex-direction: column;
					overflow: hidden;

					padding: 0;

					li {
						width: 100%;
						transition: all 250ms ease;
						margin: 0;
						&:hover {
							background-color: rgba(196,196,196,.2);

							a {
								color: black;
							}
						}
						a {
							padding: rem(15) rem(20);
							color: var(--c-black-50);
							font-size: rem(14);
							white-space: nowrap;
							box-shadow: none;
						}
					}
				}
			}

			.drop-menu.active {
				> a {
					box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
						0px 4px 4px rgba(0, 0, 0, 0.25);
					color: var(--c-red);
					i {
						transform: rotate(180deg);
					}
				}

				.drop-menu-content {
					display: flex;
				}
			}
		}

		.burger {
			width: rem(12);
			height: rem(10);

			display: flex;
			flex-direction: column;
			justify-content: space-between;

			@include min768 {
				display: none;
			}

			.line {
				width: 100%;
				height: rem(2);
				border-radius: rem(2);
				background-color: var(--c-red);
			}
		}
	}
}