.advantages {
	margin-bottom: rem(60);

	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__item {
		width: calc((100% - 4.125rem) / 4);
		margin-right: rem(22);

		display: flex;
		// flex-wrap: wrap;
		align-items: center;

		@include min1050 {
			width: calc((100% - 2.8rem) / 4);
			margin-right: rem(14);
		}

		@include min768 {
			width: calc((100% - 2.8rem) / 2);
			margin-right: 0;

			margin-bottom: rem(20);

			&:nth-child(3), &:last-child {
				margin-bottom: 0;
			}
		}

		&:last-child {
			margin-right: 0;
		}

		img {
			margin-right: rem(10);
			width: rem(30);
		}

		i {
			margin-right: rem(10);
			font-size: rem(30);
		}

		.image {
			margin-right: rem(10);
		}

		p {
			font-size: rem(12);
		}
	}
}