.modal_static {
	padding-bottom: rem(28);
	overflow: hidden;

	.modal_content__wrap {
		overflow: hidden;
	}

	.modal_content__box {
		height: 100%;
		// overflow: auto;
		padding-bottom: rem(30);
		padding-right: rem(15);

		.simplebar-track.simplebar-vertical {
			width: rem(2);
			border-radius: rem(5);
			background-color: rgba(196, 196, 196, .2);
			margin-bottom: rem(30);
		}

		.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
			z-index: 2;
			width: rem(2);
			border-radius: rem(5);
			background-color: #848484;
			opacity: 1;

			left: 0;
			right: 0;
		}
	}
}

.modal_change_city {
	.smart_menu__header-locale {
		margin-bottom: rem(60);
	}

	.list_checked {
		display: flex;
		flex-direction: column;

		&__item {
			position: relative;
			height: rem(40);
			padding: 0 rem(20);
			display: flex;
			align-items: center;
			flex-direction: row-reverse;
			justify-content: space-between;
			border-radius: rem(5);

			input {
				display: none;

				&:checked {
					~ .check {
						opacity: 1;

						&::before {
							content: "";
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background-color: rgba(225, 225, 225, .2);
						}
					}
				}
			}

			.check {
				transition: all 250ms ease;
				opacity: 0;
				i {
					font-size: rem(12);
				}
			}
		}
	}
}

.modal_catalog {
	.modal_content__sect-header {
		margin: rem(14) rem(14) rem(4) rem(14);
	}

	.modal_content__wrap {
		margin: 0 rem(14);
		padding: 0 rem(20);

		overflow-y: auto;

		padding-bottom: rem(80);
	}
}

.modal_content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: var(--c-black-20);
	z-index: 50;

	// padding: rem(14);

	

	display: none;

	.aside {
		display: block;
		flex: 0 0 100%;
		max-width: 100%;
		margin-right: 0;

		.aside__item-header {
			padding: rem(20) 0;
		}
	}

	&__sect-header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin: rem(14);

		padding: rem(20) rem(14);

		border-radius: rem(5);
		background-color: white;

		b {
			font-weight: 600;
			font-size: rem(16);
		}
	}

	&__bg {
		background-color: white;
		height: 100%;
		padding: rem(18) rem(14) rem(25) rem(14);
		overflow-y: auto;

		.modal_content__header {
			padding-bottom: rem(14);
			border-bottom: rem(0.5) solid var(--c-black-20);
			.title {
				font-size: rem(16);
			}

			.modal_close {
				position: absolute;
				top: rem(8);
				right: rem(8);
			}
		}
	}
	
	&__wrap {
		background-color: white;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
		0px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: rem(5);
		// min-height: 100vh;
		height: 100%;
		margin: rem(14);
		padding: rem(14) rem(10);
	}

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: rem(15);

		.title {
			font-weight: bold;
		}
	}

	&__box {

		// overflow-y: auto;

		.field {
			margin-bottom: rem(30);
		}

		.title {
			font-weight: bold;
			margin-bottom: rem(14);
			display: block;
		}
	}

	&__favorites {
		display: flex;
		flex-direction: column;
		overflow-y: auto;

		.item {
			display: flex;
			flex-wrap: wrap;
			border: rem(0.5) solid var(--c-black-20);
			padding: rem(8) rem(6);
			border-radius: rem(5);

			margin-bottom: rem(10);

			transition: all 250ms ease;

			&:last-child {
				margin-bottom: 0;
			}

			&:hover {

				border-color: transparent;
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
				0px 4px 4px rgba(0, 0, 0, 0.25);

				.bottom a {
					opacity: 1;
				}

				.bottom .favorite_trash {
					opacity: 1;
				}
			}

			&__left {
				flex: 0 0 rem(82);
				height: rem(64);
				border-radius: rem(5);

				margin-right: rem(16);
				overflow: hidden;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			&__right {

				flex: 1;

				display: flex;
				flex-direction: column;

				.top {
					display: flex;
					flex-direction: column;

					b {
						font-weight: 600;
						font-size: rem(10);
						margin-bottom: rem(2);
					}

					span {
						font-size: rem(8);
					}
				}

				.bottom {
					margin-top: auto;

					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;


					a {
						font-size: rem(8);
						color: var(--c-black-50);
						text-decoration: underline;

						opacity: 0;
					}

					.favorite_trash {
						transition: all 250ms ease;
						opacity: .4;
					}
				}
			}
		}
	}
}

.modal_content.active {
	display: block;
}

.smart_menu {
	position: fixed;
	top: 0;
	left: 0;

	

	&__wrap {
		background-color: white;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
		0px 4px 4px rgba(0, 0, 0, 0.25);
		border-top-right-radius: rem(5);
		border-bottom-right-radius: rem(5);

		width: rem(192);

		display: flex;
		flex-direction: column;
		height: 100%;
		overflow-y: auto;
	}

	&__header {
		padding: rem(15) rem(14);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		border-bottom: rem(0.5) solid var(--c-black-20);

		&-locale {
			display: flex;
			align-items: center;

			i {
				margin-right: rem(4);

				font-size: rem(16);
			}

			.text {
				font-weight: 500;
			}
		}
	}

	&__nav {
		padding: rem(16) rem(14) rem(16) 0;

		ul {
			li {
				a {
					display: flex;
					align-items: center;

					padding: rem(12) rem(14);
					font-weight: 500;

					background-color: transparent;

					&:hover {
						border-top-right-radius: rem(5);
						border-bottom-right-radius: rem(5);
						background-color: rgba(196, 196, 196, .2);
					}

					.icon-arrow-bottom {
						margin-left: rem(7);

						font-size: rem(6);
					}
				}
			}
		}
	}

	&__foot {
		margin-top: auto;

		border-top: rem(0.5) solid var(--c-black-20);

		padding-left: rem(14);
		padding-top: rem(22);
		padding-bottom: rem(16);

		ul {
			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					color: var(--c-black-50);
					font-size: rem(12);
					line-height: 125%;

					&:hover {
						color: var(--c-red);
					}
				}
			}
		}
	}
}

.smart_search_item {
	border-radius: rem(5);
	padding: rem(8);
	border: rem(0.5) solid var(--c-black-20);

	background-color: white;

	margin-bottom: rem(8);

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&:last-child {
		margin-bottom: 0;
	}

	img {
		border-radius: rem(5);
		width: rem(84);
		height: rem(64);

		object-fit: cover;
	}

	.text {
		margin-left: rem(17);
		display: flex;
		flex-direction: column;

		b {
			font-weight: 600;
			font-size: rem(10);
			margin-bottom: rem(2);
		}

		span {
			font-size: rem(8);
		}
	}
}

#modal_search {
	@include max1120 {
		display: none;
	}
}

#modal_catalog {
	@include max768 {
		display: none;
	}
}

#modal_smart_menu {
	@include max768 {
		display: none;
	}
}

#modal_change_locale {
	@include max768 {
		display: none;
	}
}

#modal_favorite {
	@include max768 {
		display: none;
	}
}